import React from "react";

const CalendarCard = ({ calenderData, ...props }) => {
  return (
    <div className="mi-calendar-card-container">
      {calenderData.length > 0 &&
        calenderData.map((c, i) => {
          return (
            <div className="mi-calendar-card">
              <img src={c.image} />
              <div className="mi-card-gradient"></div>
              {c.isOnline && (
                <div className="mi-calendar-card-status mi-text">online</div>
              )}
              <div className="mi-calendar-card-date mi-text">
                {c.leftDays + " days left"}
              </div>
              <div className="mi-calendar-card-data">
                <div className="mi-card-date">{c.date}</div>
                <div className="mi-card-title">{c.title}</div>
              </div>
              {c.location && (
                <div className="mi-calendar-card-location">
                  <i className="mi-icon-location" />
                  <p className="mi-p-small">{c.location}</p>
                </div>
              )}
            </div>
          );
        })}
    </div>
  );
};

export default CalendarCard;
